import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import TimeChart from './TimeChart';
import Card from '@material-ui/core/Card';
import axios from "axios";
import moment from 'moment'

import {
    Legend, Tooltip, ResponsiveContainer,
    BarChart, Bar, XAxis, YAxis, CartesianGrid
  } from 'recharts';

  
import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file
import { DateRange } from 'react-date-range';

const styles = theme => ({
  appBarSpacer: theme.mixins.toolbar,
});
  
class TrendsPage extends React.Component {
    state ={
        sleepData: [],
        dateRange: {
            selection: {
              startDate: new Date(),
              endDate: new Date(),
              key: 'selection',
            },
        },
        oxygenData: [],
        heartRateData: [],
        hr_peaks: 0,
        peak_times: [],
        degree: 1,
        coefficients: [],
        avg_hr: 0,
        average_ol: 0,
        max_hr: 0,
        max_ol: 0,
        min_hr: 0,
        min_ol: 0,
        total_time: 0,
        datapoints: 0,
    }

    updateData(query){
      console.log("update data");
      const url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/compute/?" + query;
      axios
      .get(url)
      .then(({data}) => {
        this.setState(
          {
            hr_peaks: data.num_peaks,
            peak_times: data.sleepdata_time,
            degree: data.degree,
            coefficients: data.coefficients,
          }
        );
      })
      .catch((err)=> {})

      const query_url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/query-summary/?" + query;
      axios
      .get(query_url)
      .then(({data}) => {
        this.setState(
          {
            avg_hr: data.average_hr.heart_rate__avg,
            avg_ol: data.average_ol.oxygen_level__avg,
            max_hr: data.max_hr.heart_rate__max,
            max_ol: data.max_ol.oxygen_level__max,
            min_hr: data.min_hr.heart_rate__min,
            min_ol: data.min_ol.oxygen_level__min,
            total_time: data.total_time,
            datapoints: data.total_count,
          }
        );
      })
      .catch((err)=> {})
    }


    updateGraph(query){
        console.log("updating graph")
        const url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/sleepdata/?" + query;
        axios
          .get(url)
          .then(({data}) => {
            this.setState(
              {sleepData: data}
            );
          })
          .catch((err)=> {})
          console.log(this.state.sleepData);

        const ol_url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/compute/?" + query;
        axios
          .get(ol_url)
          .then(({data}) => {
            this.setState(
              {oxygenData: data.ol_bins, 
              heartRateData: data.hr_bins
              }
            );
          })
          .catch((err)=> {})
          console.log(this.state.ol_bins);
          console.log(this.state.hr_bins);

          this.updateData(query);
        }

    handleRangeChange(which, payload) {
        console.log(which, payload);
        this.setState({
          [which]: {
            ...this.state[which],
            ...payload,
          },
        });  
        var start_date= moment(this.state.dateRange.selection.startDate).format('YYYY-MM-DD');
        var end_date = moment(this.state.dateRange.selection.endDate).format('YYYY-MM-DD');
        var query;
        if (start_date == end_date){
            query = "date=" + start_date;
        } else{
            query = "start_date=" + start_date + "&end_date=" + end_date; 
        }
        this.updateGraph(query)
    }

    componentDidMount(){
        console.log("ComponentDidMount");  
        var dateTo = moment().add(1,'d').format('YYYY-MM-DD');
        var query = "start_date=2019-02-08&end_date=" + dateTo;
        const url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/sleepdata/?" + query
        axios
          .get(url)
          .then(({data}) => {
            console.log(data);
            this.setState(
              {sleepData: data}
            );
          })
          .catch((err)=> {})
          console.log(this.state.sleepData);

          const ol_url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/compute/?" + query;
          axios
            .get(ol_url)
            .then(({data}) => {
              this.setState(
                {oxygenData: data.ol_bins, 
                heartRateData: data.hr_bins
                }
              );
            })
            .catch((err)=> {})
            console.log(this.state.ol_bins);
            console.log(this.state.hr_bins);
  
            this.updateData(query);
      }

  render() {
    const { classes } = this.props;
    var CurrentDate = moment().format('dddd, MMMM Do');

    return (
            <div style={{flex: 1}}>
                <div className={classes.appBarSpacer} />
                <div style={{flex: 1, display: 'flex', width: '100%', height: '85vh'}}>
                    <Card style={{display: 'flex', width: '100%', flexDirection: 'row' }}>
                            <div style={{display: 'flex', flex: 1}}>
                                    <div style={{display: 'flex', flex: 1, flexDirection: 'column', height: '100%'}}>
                                        <div style={{display: 'flex', flexDirection: 'column', alignItems: 'flex-start', justifyContent: 'space-between', padding: '18px 40px'}}>
                                            <Typography variant='h6' style={{fontWeight: 'bold', color: '#686868'}}>Sleep Data Overview</Typography>
                                            <Typography variant='subtitle2' style={{fontStyle: 'light', color: '#888888'}}>As of {CurrentDate}</Typography>
                                        </div>
                                            <TimeChart chartData={this.state.sleepData}/>
                                    </div>
                                    <div style={{display: 'flex', flexDirection: 'column', marginRight: 40, justifyContent: 'flex-start', paddingTop: '18px'}}>
                                        <Typography variant='h6' style={{fontWeight: 'bold', color: '#686868'}}>Data Time Range</Typography> 
                                        <Typography variant='subtitle2' style={{fontStyle: 'light', color: '#888888', marginBottom: 20}}>Data available up to {CurrentDate}</Typography>   
                                        <DateRange
                                            onChange={this.handleRangeChange.bind(this, 'dateRange')}
                                            moveRangeOnFirstSelection={false}
                                            ranges={[this.state.dateRange.selection]}
                                            className={'PreviewArea'}
                                            style={{width: '40vh'}}
                                        />
                                    </div>
                                </div>
                    </Card>
                </div>
                <div style={{width: '100%', height: '60vh'}}>
                    <Card style={{display: 'flex', width: '100%', flexDirection: 'row', height: '70vh', marginTop: 15, marginBottom: 15}}>
                        <div style={{display: 'flex', flex: 1, flexDirection: 'column', height: '100%'}}>
                            <div style={{display: 'flex', flexDirection: 'column', alignItems: 'flex-start', justifyContent: 'space-between', padding: '18px 40px'}}>
                                <Typography variant='h6' style={{fontWeight: 'bold', color: '#686868'}}>Summary Statistics</Typography>
                                <Typography variant='subtitle2' style={{fontStyle: 'light', color: '#888888'}}>As of {CurrentDate}</Typography>
                            </div>
                            <div style={{display: 'flex', flex: 1, justifyContent: 'flex-start'}}>
                                <ResponsiveContainer width="35%" height="85%">
                                    <BarChart data={this.state.oxygenData}
                                        margin={{top: 5, right: 30, left: 20, bottom: 5}}>
                                        <CartesianGrid strokeDasharray="3 3"/>
                                        <XAxis
                                          dataKey = 'oxygen_bin'
                                          domain = {['auto', 'auto']}
                                          name = 'Percentage'/>
                                        <YAxis/>
                                        <Tooltip/>
                                        <Legend />
                                        <Bar dataKey="oxygen_level" fill="#8884d8" />
                                </BarChart>
                                </ResponsiveContainer>
                                <div style={{width:"12%",height:"85%"}}>
                                <div style={{display: 'flex', flexDirection: 'column', paddingTop: 10}}>
                                    <Typography variant='subtitle1' style={{fontWeight: 'bold', color: '#696969'}}>Oxygen Level Stats</Typography>
                                    <div style={{display: 'flex', justifyContent: 'space-around', paddingTop: 10}}>
                                        <div style={{display: 'flex', flexDirection: 'column', alignItems: 'flex-start'}}>
                                            <Typography variant='subtitle2' style={{paddingBottom: 10, fontWeight: 'bold', color: '#888888'}}>Total Hours</Typography>
                                            <Typography variant='subtitle2' style={{paddingBottom: 10, fontWeight: 'bold', color: '#888888'}}>Data Points</Typography>
                                            <Typography variant='subtitle2' style={{paddingBottom: 10, fontWeight: 'bold', color: '#888888'}}>Max Oxygen</Typography>
                                            <Typography variant='subtitle2' style={{paddingBottom: 10, fontWeight: 'bold', color: '#888888'}}>Min Oxygen</Typography>
                                            <Typography variant='subtitle2' style={{paddingBottom: 10, fontWeight: 'bold', color: '#888888'}}>Average Oxygen</Typography>
                                            <Typography variant='subtitle2' style={{paddingBottom: 10, fontWeight: 'bold', color: '#888888'}}>Number of Peaks</Typography>
                                            <Typography variant='subtitle2' style={{paddingBottom: 10, fontWeight: 'bold', color: '#888888'}}>Estimated Sleep Cycles</Typography>
                                        </div>
                                        <div style={{display: 'flex', flexDirection: 'column', alignItems: 'flex-end', paddingTop: 2}}>
                                            <Typography style={{paddingBottom: 11, color: '#888888'}}>{parseFloat(Math.round(this.state.total_time * 100) / 100).toFixed(2)}</Typography>
                                            <Typography style={{paddingBottom: 12, color: '#888888'}}>{this.state.datapoints}</Typography>
                                            <Typography style={{paddingBottom: 12, color: '#888888'}}>{this.state.max_ol}</Typography>
                                            <Typography style={{paddingBottom: 12, color: '#888888'}}>{this.state.min_ol}</Typography>
                                            <Typography style={{paddingBottom: 11, color: '#888888'}}>{Math.floor(this.state.avg_ol)}</Typography>
                                            <Typography style={{paddingBottom: 11, color: '#888888'}}>{this.state.hr_peaks}</Typography>
                                            <Typography style={{paddingBottom: 11, color: '#888888'}}>{this.state.hr_peaks}</Typography>
                                        </div>
                                    </div>
                                </div>
                                </div>
                                <ResponsiveContainer width="35%" height="85%">
                                    <BarChart data={this.state.heartRateData}
                                            margin={{top: 5, right: 30, left: 20, bottom: 5}}
                                            title="Heart Rate Bins">
                                            
                                    <CartesianGrid strokeDasharray="3 3"/>
                                    <XAxis
                                          dataKey = 'heart_rate_bin'
                                          domain = {['auto', 'auto']}
                                          name = 'bpm'/>
                                    <YAxis/>
                                    <Tooltip/>
                                    <Legend />
                                    <Bar dataKey="heart_rate" fill="#82ca9d" />
                                    </BarChart>
                                </ResponsiveContainer>
                                <div style={{width:"12%",height:"85%"}}>
                                <div style={{display: 'flex', flexDirection: 'column', paddingTop: 10}}>
                                    <Typography variant='subtitle1' style={{fontWeight: 'bold', color: '#696969'}}>Heart Rate Stats</Typography>
                                    <div style={{display: 'flex', justifyContent: 'space-around', paddingTop: 10}}>
                                        <div style={{display: 'flex', flexDirection: 'column', alignItems: 'flex-start'}}>
                                            <Typography variant='subtitle2' style={{paddingBottom: 10, fontWeight: 'bold', color: '#888888'}}>Total Hours</Typography>
                                            <Typography variant='subtitle2' style={{paddingBottom: 10, fontWeight: 'bold', color: '#888888'}}>Data Points</Typography>
                                            <Typography variant='subtitle2' style={{paddingBottom: 10, fontWeight: 'bold', color: '#888888'}}>Max Heart Rate</Typography>
                                            <Typography variant='subtitle2' style={{paddingBottom: 10, fontWeight: 'bold', color: '#888888'}}>Min Heart Rate</Typography>
                                            <Typography variant='subtitle2' style={{paddingBottom: 10, fontWeight: 'bold', color: '#888888'}}>Average Heart Rate</Typography>
                                            <Typography variant='subtitle2' style={{paddingBottom: 10, fontWeight: 'bold', color: '#888888'}}>Number of Peaks</Typography>
                                        </div>
                                        <div style={{display: 'flex', flexDirection: 'column', alignItems: 'flex-end'}}>
                                            <Typography style={{paddingBottom: 12, color: '#888888'}}>{parseFloat(Math.round(this.state.total_time * 100) / 100).toFixed(2)}</Typography>
                                            <Typography style={{paddingBottom: 12, color: '#888888'}}>{this.state.datapoints}</Typography>
                                            <Typography style={{paddingBottom: 12, color: '#888888'}}>{this.state.max_hr}</Typography>
                                            <Typography style={{paddingBottom: 12, color: '#888888'}}>{this.state.min_hr}</Typography>
                                            <Typography style={{paddingBottom: 12, color: '#888888'}}>{Math.floor(this.state.avg_hr)}</Typography>
                                            <Typography style={{paddingBottom: 12, color: '#888888'}}>{this.state.hr_peaks}</Typography>
                                        </div>
                                    </div>
                                </div>
                                </div>
                            </div>
                        </div>
                    </Card>
                </div>
            </div>
    );
  }
}



TrendsPage.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(TrendsPage);