import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import PredictionsChart from './PredictionsChart';
import Card from '@material-ui/core/Card';
import axios from "axios";
import moment from 'moment'
import Divider from '@material-ui/core/Divider';
  
import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file
import { DateRange } from 'react-date-range';
const styles = theme => ({
    appBarSpacer: theme.mixins.toolbar,
});

class PredictionsPage extends React.Component {
    state ={
        sleepData: [],
        dateRange: {
            selection: {
              startDate: new Date(),
              endDate: new Date(),
              key: 'selection',
            },
        },
        oxygenData: [],
        heartRateData: [],
        hr_peaks: 0,
        peak_times: [],
        degree: 1,
        coefficients: [],
        avg_hr: 0,
        average_ol: 0,
        max_hr: 0,
        max_ol: 0,
        min_hr: 0,
        min_ol: 0,
        total_time: 0,
        datapoints: 0,
        predictions: [],
        analysis: "",
        explanation: "",
        recommendation: "",
        observation: "",
        imgUrl: "",
    }

    updateData(query){
      console.log("update data");
      const url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/compute/?" + query;
      axios
      .get(url)
      .then(({data}) => {
        this.setState(
          {
            hr_peaks: data.num_peaks,
            peak_times: data.sleepdata_time,
            degree: data.degree,
            coefficients: data.coefficients,
          }
        );
      })
      .catch((err)=> {})

      const query_url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/query-summary/?" + query;
      axios
      .get(query_url)
      .then(({data}) => {
        this.setState(
          {
            avg_hr: data.average_hr.heart_rate__avg,
            avg_ol: data.average_ol.oxygen_level__avg,
            max_hr: data.max_hr.heart_rate__max,
            max_ol: data.max_ol.oxygen_level__max,
            min_hr: data.min_hr.heart_rate__min,
            min_ol: data.min_ol.oxygen_level__min,
            total_time: data.total_time,
            datapoints: data.total_count,
          }
        );
      })
      .catch((err)=> {})
      this.updateAnalysis();
    }


    updateGraph(query){
        console.log("updating graph")
        const url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/compute/?" + query;
        console.log(url);
        axios
          .get(url)
          .then(({data}) => {
            this.setState(
              {predictions: data.function}
            );
          })
          .catch((err)=> {})
          console.log(this.state.predictions);
          console.log(this.state.degree);
          this.updateData(query);
        }

    handleRangeChange(which, payload) {
        console.log(which, payload);
        this.setState({
          [which]: {
            ...this.state[which],
            ...payload,
          },
        });  
        var start_date= moment(this.state.dateRange.selection.startDate).format('YYYY-MM-DD');
        var end_date = moment(this.state.dateRange.selection.endDate).format('YYYY-MM-DD');
        end_date = moment(end_date).add(1,'d').format('YYYY-MM-DD');
        var query;
        if (start_date == end_date){
            query = "date=" + start_date;
        } else{
            query = "start_date=" + start_date + "&end_date=" + end_date; 
        }
        this.updateGraph(query);
    }

    componentDidMount(){
        console.log("ComponentDidMount");
        var CurrentDate = moment().format('YYYY-MM-DD');
        const url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/sleepdata/?date=" + CurrentDate;
        axios
          .get(url)
          .then(({data}) => {
            console.log(data);
            this.setState(
              {sleepData: data,}
            );
          })
          .catch((err)=> {})
          console.log(this.state.sleepData);
        
          const func_url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/compute/?date=" + CurrentDate;
          axios
            .get(func_url)
            .then(({data}) => {
              console.log(data);
              this.setState(
                {predictions: data.function, 
                degree: data.degree}
              );
            })
            .catch((err)=> {})
            console.log(this.state.degree);
            this.updateAnalysis();
      };

      updateAnalysis(){
          if (this.state.degree == 3){
              this.setState(
                  {
                      analysis: "Dune Curve",
                      observation: "Your heart rate increases right after you fall asleep.",
                      explanation: "The increase in your heart rate may be a sign that you're too tired for bed. Your body may be feeling the effects of increase melatonin and lowered blood pressure.",
                      recommendation: "Try keeping a steady sleep routine to help you get better sleep and preform better during the day.",
                      imgUrl: 'https://d1a0efioav7lro.cloudfront.net/wp-content/uploads/2018/08/30111904/heart-rate-curve-dune-final.png'
                    }
              )
          } else if (this.state.degree == 2){
                this.setState(
                    {
                        analysis: "Hammock Curve",
                        observation: "Your body relaxes and heart rate drops during the first sleep cycles, and your lowest heart rate occurs in the midpoint of sleep.",
                        explanation: "In your midpoint of sleep, the amount of melatonin is at its highest and thus corresponds with your lowest heart rate.",
                        recommendation: "Keep up your current schedule.",
                        imgUrl: 'https://d1a0efioav7lro.cloudfront.net/wp-content/uploads/2018/08/30111901/heart-rate-curve-hammock-final.png'
                    }
                )
          } else if (this.state.degree == 1){
                this.setState(
                    {
                        analysis: "Downwards Slope",
                        observation: "Your heart rate readings trend downwards.",
                        explanation: "The negative slope may be an indication that your metabolism is working overtime. This may be due to a later meal or workout than usual.",
                        recommendation: "If you're a late exerciser, try exercising 1-2 hours earlier before sleep and avoiding late meals.",
                        imgUrl: 'https://d1a0efioav7lro.cloudfront.net/wp-content/uploads/2018/08/30111907/heart-rate-curve-downward-slope-final.png'
                    }
                )
          } else {
              this.setState(
                  {
                    analysis: "NA",
                    observation: "NA", 
                    explanation: "Not enough data.",
                    recommendation: "NA",
                    imgUrl: 'https://www.lifewire.com/thmb/OO7CD06NAdoIwv71DgUgBiTd4ps=/768x0/filters:no_upscale():max_bytes(150000):strip_icc()/shutterstock_325494917-5a68d8403418c600190a3e1f.jpg'
                  }

              )
          }

          console.log("analysis: " + this.state.analysis);
          console.log("observation: " + this.state.observation);
          console.log("explanation: " + this.state.explanation);
          console.log("recommendation: " + this.state.recommendation);
      }

  render() {
    const { classes } = this.props;
    var CurrentDate = moment().format('dddd, MMMM Do');

    return (
            <div style={{flex: 1}}>
                <div className={classes.appBarSpacer} />
                <div style={{flex: 1, display: 'flex', width: '100%', height: '85vh'}}>
                    <Card style={{display: 'flex', width: '100%', flexDirection: 'row' }}>
                            <div style={{display: 'flex', flex: 1}}>
                                    <div style={{display: 'flex', flex: 1, flexDirection: 'column', height: '100%'}}>
                                        <div style={{display: 'flex', flexDirection: 'column', alignItems: 'flex-start', justifyContent: 'space-between', padding: '18px 40px'}}>
                                            <Typography variant='h6' style={{fontWeight: 'bold', color: '#686868'}}>Sleep Data Overview</Typography>
                                            <Typography variant='subtitle2' style={{fontStyle: 'light', color: '#888888'}}>As of {CurrentDate}</Typography>
                                        </div>
                                            <PredictionsChart chartData={this.state.predictions}/>
                                    </div>
                                    <div style={{display: 'flex', flexDirection: 'column', marginRight: 40, justifyContent: 'flex-start', paddingTop: '18px'}}>
                                        <Typography variant='h6' style={{fontWeight: 'bold', color: '#686868'}}>Data Time Range</Typography> 
                                        <Typography variant='subtitle2' style={{fontStyle: 'light', color: '#888888', marginBottom: 20}}>Data available up to {CurrentDate}</Typography>   
                                        <DateRange
                                            onChange={this.handleRangeChange.bind(this, 'dateRange')}
                                            moveRangeOnFirstSelection={false}
                                            ranges={[this.state.dateRange.selection]}
                                            className={'PreviewArea'}
                                            style={{width: '40vh'}}
                                        />
                                    </div>
                                </div>
                    </Card>
                </div>
                <div style={{width: '100%', height: '60vh'}}>
                    <Card style={{display: 'flex', width: '100%', flexDirection: 'row', height: '70vh', marginTop: 15, marginBottom: 15}}>
                        <div style={{display: 'flex', flex: 1, flexDirection: 'column', height: '100%'}}>
                            <div style={{display: 'flex', flexDirection: 'column', alignItems: 'flex-start', justifyContent: 'space-between', padding: '18px 40px'}}>
                                <Typography variant='h6' style={{fontWeight: 'bold', color: '#686868'}}>Sleep Analysis</Typography>
                                <Typography variant='subtitle2' style={{fontStyle: 'light', color: '#888888'}}>For {moment(this.state.dateRange.selection.startDate).format('YYYY-MM-DD')}</Typography>
                            </div>
                            <div style={{display: 'flex', flex: 1}}>
                                <div style={{display: 'flex', width: '50%', paddingLeft: '50px', paddingBottom: '50px'}}>
                                    <img src={this.state.imgUrl} style={{width:'100%', height:'100%'}}></img>
                                </div>
                                <div style={{display: 'flex', flexDirection: 'column', flex: 1, justifyContent: 'space-between', marginLeft: 50, marginRight: 50, marginBottom: 50}}>
                                    <div style={{display: 'flex', flexDirection: 'column'}}>
                                        <Typography variant='h5' style={{fontWeight: 'bold',  color: '#686868'}}>Analysis</Typography>
                                        <Divider/>
                                    </div>
                                    <Typography variant="h4" style={{fontWeight: 'bold',  color: '#48A4C1'}}>{this.state.analysis}</Typography>
                                    <div style={{display: 'flex', flexDirection: 'column'}}>
                                        <Typography variant='h5' style={{fontWeight: 'bold',  color: '#686868'}}>Observations</Typography>
                                        <Divider/>
                                    </div>
                                    <Typography variant="h6">{this.state.observation}</Typography>
                                    <div style={{display: 'flex', flexDirection: 'column'}}>
                                        <Typography variant='h5' style={{fontWeight: 'bold',  color: '#686868'}}>Explanation</Typography>
                                        <Divider/>
                                    </div>
                                    <Typography variant="h6">{this.state.explanation}</Typography>
                                    <div style={{display: 'flex', flexDirection: 'column'}}>
                                        <Typography variant='h5' style={{fontWeight: 'bold',  color: '#686868'}}>Recommendations</Typography>
                                        <Divider/>
                                    </div>
                                    <Typography variant="h6">{this.state.recommendation}</Typography>
                                </div>
                        </div>
                    </div>
                    </Card>
                </div>
            </div>
    );
  }
}



PredictionsPage.propTypes = {
    classes: PropTypes.object.isRequired,
  };
  
  export default withStyles(styles)(PredictionsPage);