import React from 'react';
import ResponsiveContainer from 'recharts/lib/component/ResponsiveContainer';
import AreaChart from 'recharts/lib/chart/AreaChart';
import Area from 'recharts/lib/cartesian/Area';
import XAxis from 'recharts/lib/cartesian/XAxis';
import YAxis from 'recharts/lib/cartesian/YAxis';
import CartesianGrid from 'recharts/lib/cartesian/CartesianGrid';
import Tooltip from 'recharts/lib/component/Tooltip';
import Legend from 'recharts/lib/component/Legend';
import Card from '@material-ui/core/Card';
import moment from 'moment';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';

const styles = {
    card: {
        width: '100%',
        height: 520,
        display: 'flex',
        alignItems: 'center',
    },
  };

function formatXAxis(tickItem){
    var date = new Date(tickItem).toISOString();
    return moment(date).utcOffset('-0400').format('hh:mm');
}


const SummaryGraph = ({ chartData, date }) => (
  <Card style={styles.card} className={{minWidth: 300}}>
  <div style={{flex: 1, display: 'flex', flexDirection: 'column', height: '100%', width: '100%',justifyContent: 'space-between', }}>        
          <div style={{ height: '20%', display: 'flex', justifyContent: 'space-between', paddingTop: '20px',paddingLeft: '18px', paddingRight: '5px'}}>
              <div style={{display: 'flex', flexDirection: 'column', alignItems: 'flex-start'}}>
                  <Typography variant='h6' style={{fontWeight: 'bold', color: '#686868'}}>Weekly Sleep Summary</Typography>
                  <Typography variant='subtitle2' style={{fontStyle: 'light', color: '#888888'}}>As of {date}</Typography>
              </div>
              <IconButton style={{alignSelf: 'flex-start'}}>
                  <MoreVertIcon />
              </IconButton>
          </div>
  <div style={{flex: 1, width: '99%', height: '100%', marginBottom: 10, alignItems: 'flex-start', justifyContent: 'center', display: 'flex'}}>
   <ResponsiveContainer width="95%" height="90%">
      <AreaChart data={chartData}
          margin={{top: 5, right: 30, left: 20, bottom: 5}}>
    <XAxis
      dataKey = 'date'
      domain = {['auto', 'auto']}
      name = 'Time'
      tickFormatter = {(date) => formatXAxis(date)}
    />
      <YAxis
          domain={['dataMin - 20', 'dataMax + 20']}/>
      <CartesianGrid strokeDasharray="3 3"/>
      <Tooltip/>
      <Legend />
      <Area type="monotone" dataKey="heart_rate" stroke="#2C839F" fill='#2C839F'  activeDot={{r: 8}}/>
      <Area type="monotone" dataKey="oxygen_level" stroke="#8884d8" fill='#8884d8'  activeDot={{r: 8}}/>
      </AreaChart>
  </ResponsiveContainer>
        </div>
    </div>
    </Card>
)

export default SummaryGraph;