import React, {Component} from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Calendar from 'react-calendar';
import Card from '@material-ui/core/Card';
import AlarmList from "./AlarmList";
import moment from 'moment'
import axios from "axios";
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';


let imgUrl = 'https://i.pinimg.com/originals/0d/04/4c/0d044c10cb49a675389bc0e2ff66226b.jpg';

const theme_styles = theme => ({
    appBarSpacer: theme.mixins.toolbar,
});


const styles = {
    button: {
        margin: 10, 
        width: '75%', 
        color: '#4D8FA3',
        backgroundColor: 'transparent', 
        alignSelf: 'center', 
        marginTop: 30
    },
    card: {
        width: '100%', 
        height: '80%',
        backgroundImage: 'url(' + imgUrl + ')',
        backgroundSize: 'cover',
        overflow: 'hidden',
        justifyContent: 'center'
    },
    container: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    textField: {
        marginLeft: 10,
        marginRight: 10,
    },
};

class AlarmsPage extends Component{

    constructor(props){
        super(props)
        this.state = {
            date: new Date(),
            alarms: [],
            time: '2:15 am',
            layout: 'horizontal',
            showingOptions: true,
            validateOnSubmit: false,
            validatePristine: false,
            disabled: false,
            songs: [
                "",
                "",
                "",
                "",
                "" ],
        }
        this.handleTimeChange = this.handleTimeChange.bind(this)
    }

    handleChangeOption = (name, value) => {
        const newState = {};
        newState[name] = value;
        this.setState(newState);
      };

      handleChange = name => event => {
        this.setState({
          [name]: event.target.value,
        });
      };

      handleChangeHelp(event, index){
          console.log("index is: " + index);
          console.log(event.target.value)
          var newSongs = this.state.songs;
          newSongs[index] = event.target.value;
          this.setState({
              songs: newSongs,
          });
          console.log(this.state.songs);
      };
    
      handleToggleOptions = () => {
        const {showingOptions} = this.state;
        this.setState({showingOptions: !showingOptions});
      };
    

    componentDidMount(){
        console.log("ComponentDidMount");
        const url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/alarms/"
        axios
          .get(url)
          .then(response => {
              console.log("Response: ", response);
              const newAlarms = response.data.map(a => {
                return {
                  id: a.id,
                  description: a.description,
                  alarm_time: a.alarm_time,
                  volume: a.volume,
                  active: a.active,
                };
              });
    
              const newState = Object.assign({}, this.state, {
                alarms: newAlarms
              });
    
              this.setState(newState);
          })
          .catch(error => console.log(error));
      }
    

    handleSubmit(){
        //console.log(this.state.songs); 
        const url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/songs/"
        var i;
        console.log("BUTTON WAS CLICKED AHHHH")

        for (i = 0; i < 5; i++){
            if (this.state.songs[i] === "") {
                continue;
            }
            axios.post( url, {
                youtube_link: this.state.songs[i]
            }).then(function(response){
                console.log(response);
            }).catch(function(error){
                console.log(error);
            });
        }

        this.setState({
            songs: [
                "", 
                "", 
                "", 
                "", 
                ""
            ]
        })
    }

    onChange = date => {
        this.setState({ date })
        var date = moment(this.state.date).format('YYYY-MM-DD');
        console.log(date);
        const url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/alarms/?date=" + date;
        axios
          .get(url)
          .then(response => {
              console.log("Response: ", response);
              const newAlarms = response.data.map(a => {
                return {
                  id: a.id,
                  description: a.description,
                  alarm_time: a.alarm_time,
                  volume: a.volume,
                  active: a.active,
                };
              });
    
              const newState = Object.assign({}, this.state, {
                alarms: newAlarms
              });
    
              this.setState(newState);
          })
          .catch(error => console.log(error));
          console.log(this.state.alarms);
    }

    handleTimeChange = newTime=> {
        console.log(newTime);
        this.setState({ time: newTime.formatted});
    }

    render() {

        return(
            <div style={{flex: 1, height: 'auto'}}>
                <div className={theme_styles.appBarSpacer} style={{marginBottom: 60}}/>
                <div style={{display: 'flex', justifyContent: 'space-around'}}>
                    <div style={{display: 'flex', flexDirection: 'column', width: '40%'}}>
                    <Card style={{width: '100%', height: '50%', display: 'flex'}}>
                        <div style={{flex: 1, display: 'flex', flexDirection: 'column', marginTop: 20, }}>
                            <Typography variant="subtitle1" style={{fontWeight: 'bold', color: '#686868'}}>Submit Preferred Alarm Songs</Typography>
                            <form noValidate autoComplete="off">
                            <div style={{flex: 1, display: 'flex', justifyContent: 'center'}}>
                                <div style={{display: 'flex', flexDirection: 'column', width: '85%', justifyContent: 'space-between'}}>
                                <TextField
                                    id="outlined-name"
                                    label="Youtube Link"
                                    value="Link #1"
                                    className={styles.textField}
                                    value={this.state.songs[0]}
                                    margin="normal"
                                    variant="outlined"
                                    onChange={(e) => this.handleChangeHelp(e, 0)}
                                    />
                                <TextField
                                    id="outlined-name"
                                    label="Youtube Link"
                                    value="Link #2"
                                    className={styles.textField}
                                    value={this.state.songs[1]}
                                    margin="normal"
                                    variant="outlined"
                                    onChange={(e) => this.handleChangeHelp(e, 1)}
                                    />
                                <TextField
                                    id="outlined-name"
                                    label="Youtube Link"
                                    value="Link #3"
                                    margin="normal"
                                    variant="outlined"
                                    onChange={(e) => this.handleChangeHelp(e, 2)}
                                    className={styles.textField}
                                    value={this.state.songs[2]}
                                    />
                                <TextField
                                    id="outlined-name"
                                    label="Youtube Link"
                                    value="Link #4"
                                    margin="normal"
                                    variant="outlined"
                                    onChange={(e) => this.handleChangeHelp(e, 3)}
                                    className={styles.textField}
                                    value={this.state.songs[3]}
                                    />
                                <TextField
                                    id="outlined-name"
                                    label="Youtube Link"
                                    value="Link #5"
                                    margin="normal"
                                    variant="outlined"
                                    onChange={(e) => this.handleChangeHelp(e, 4)}
                                    className={styles.textField}
                                    value={this.state.songs[4]}
                                    />
                                <Button variant="outlined" color="blue" style={styles.button} onClick={() => {this.handleSubmit()}}>
                                    SUBMIT
                                </Button>
                                </div>
                                </div>
                            </form>
                        </div>
                    </Card>
                    </div>
                    <div style={{display: 'flex', flexDirection: 'column', width: '25%'}}>
                        <AlarmList alarms={this.state.alarms}/>
                    </div>
                    <div style={{display: 'flex', flexDirection: 'column', marginLeft: 10, justifyContent: 'flex-start', alignItems: 'center', width:'30%', height: '125vh'}}>
                        <Card style={{width: '100%', marginBottom: 20}}>
                            <div style={{display: 'flex', flexDirection: 'column', justifyContent: 'flex-start', paddingTop: '18px'}}>
                                <Typography variant='h6' style={{fontWeight: 'bold', color: '#686868'}}>Alarms Time Range</Typography> 
                                <Typography variant='subtitle2' style={{fontStyle: 'light', color: '#888888', marginBottom: 20}}>View Alarms scheduled for Selected Date</Typography>        
                                <div style={{display: 'flex', flexDirection: 'row',justifyContent: 'center', width: '100%', marginBottom: 165}}>
                                    <Calendar
                                        onChange={this.onChange}
                                        value={this.state.date}
                                    />
                                </div>
                            </div>
                        </Card>
                        {/* <Card style={styles.card}>
                            <Typography>Im sick</Typography>
                        </Card> */}
                    </div>
                </div>
            </div>
        )
    }
}

AlarmsPage.propTypes = {
    classes: PropTypes.object.isRequired,
  };
  
  export default withStyles(styles)(AlarmsPage);